@import url(https://fonts.googleapis.com/css2?family=Cinzel:wght@400;500&family=Spectral:ital,wght@0,200;1,200&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Comic+Neue:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


*{
box-sizing: border-box;
}

body{
  width: 100vw;
  height: 100vh;
  background-color: black;
  color: aliceblue;
  font-family: 'Cinzel', serif;
  font-family: 'Spectral', serif;
  max-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}
.nav-btn{
  background-color: rgb(255, 236, 70);
  position: fixed;
  top:0;
  left: 0;
  color: black;
}
.clock{

  background-color: rgb(22, 21, 21);
  margin: 20px;
  border-style: ridge;
  border-width: 2px;
  border-radius: 10px;
  border-color: rgb(255, 255, 255);
}

@media only screen and (max-width: 470px) {
  .clock{
    top:5vh;
    margin: 10px;
  }

}

button{
  background-color: black;
  border-radius: 5px;
  color: rgb(255, 255, 255);
  font-size: 20px;
  padding: 10px;
  margin: 7px;
  cursor: pointer;
}
button :active {
  -webkit-transform: scale(0.5);
          transform: scale(0.5);
}
.flex{
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}
@media only screen and (max-width: 470px) {
  .flex-responsive{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
  }

}

.regultors{
  background-color:rgb(155, 155, 155);
  border-radius: 10px ;
}
.timer-container{
  padding: 5px;
  margin: 15px;
  background-color:rgb(66, 66, 66);
  border-radius: 10px;
  border-width: 5px  0 0 5px;
  border-color: rgb(39, 38, 38);
  border-style: ridge;
}
.timer-container h3{
    text-align: center;
    color: rgb(255, 255, 255);
    line-height: 10px;
}
@media only screen and (max-width: 470px) {
  .timer-container{
    padding: 2.5px;
    margin: 10px;
    background-color:rgb(66, 66, 66);
    border-radius: 10px;
    border-width: 5px  0 0 5px;
    border-color: rgb(39, 38, 38);
    border-style: ridge;
  }
}
.actions-wrpper span{
  color: black;
   font-size: 40px;
   margin: 0 10px;
   width: 40px;
}
.clock-box-continer{

  text-align: center;
  display: inline-block;
  padding: 10px;
  width: 400px;
  height: auto;
}
.clock-box-continer p{
  font-family: 'Spectral', serif;
  text-align: center;
  line-height: 20px;
  margin: 29px;
  
}
@media only screen and (max-width: 470px) {
  .clock-box-continer{
    text-align: center;
    display: inline-block;
    padding: 15px;
    width: auto;
    height: auto;
  }

}

.timer{
  margin: 5px;
  border: 2px solid white;
  height:100px;
  padding: 20px;
  font-size: 60px;
  border-radius: 10px;
}
.spinMovement{
  -webkit-animation-name: spin;
          animation-name: spin;
  -webkit-animation-duration: 800ms;
          animation-duration: 800ms;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out; 
}
@-webkit-keyframes spin {
  from {
      -webkit-transform:rotate(0deg);
              transform:rotate(0deg);
  }
  to {
      -webkit-transform:rotate(360deg);
              transform:rotate(360deg);
  }
}
@keyframes spin {
  from {
      -webkit-transform:rotate(0deg);
              transform:rotate(0deg);
  }
  to {
      -webkit-transform:rotate(360deg);
              transform:rotate(360deg);
  }
}
.footertext{
  text-align: right;
  color:rgb(158, 155, 155);
  position:fixed;
  width: 98vw;
  left: 0px;
  bottom:0px;
  font-size: 15px;
  
}
.mt-4{
  margin-top: 20px;
}
.no-decoration{
  text-decoration: none;
  color:rgb(199, 182, 182);
}

